/* Main container for the post detail page */
.post-detail-container {
    max-width: 600px;
    margin: 20px auto; 
    padding: 15px; 
    background-color: #fff; 
    border-radius: 8px; 
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1); 
}

/* Back button styling */
.back-button {
    position: relative; 
    margin-bottom: 15px; 
    cursor: pointer; 
}

/* Back arrow styling */
.back-arrow {
    font-size: 24px; 
    color: #3A213E; 
}

/* Post image styling */
.post-detail-image {
    width: 100%; 
    height: auto; 
    border-radius: 8px; 
}

/* Header for post details */
.post-detail-header {
    display: flex; 
    align-items: center; 
    margin-bottom: 10px; 
}

/* User image in the post header */
.post-user-img {
    width: 40px; 
    height: 40px; 
    border-radius: 50%; 
    margin-right: 10px; 
}

/* Username styling */
.post-username {
    font-weight: bold; 
    color: #262626;
    font-size: 16px;
}

/* Caption styling */
.post-detail-caption {
    margin: 10px 0; 
    font-size: 14px; 
    color: #333; 
}

/* Comments section styling */
.comments-section {
    margin-top: 15px; /* Space above comments section */
}

/* Comments list styling */
.comments-list {
    margin-top: 10px; /* Space above comments list */
}

/* Individual comment styling */
.comment {
    margin: 5px 0; /* Space between comments */
    color: #555; /* Gray color for comments */
    font-size: 14px; /* Font size for comments */
}

/* Responsive Design for Tablets */
@media screen and (max-width: 768px) {
    .post-detail-container {
        padding: 10px; /* Adjust padding for smaller screens */
    }

    .post-username {
        font-size: 14px; /* Smaller font size for username */
    }

    .post-detail-caption {
        font-size: 12px; /* Smaller font size for caption */
    }
}

/* Responsive Design for Mobile Devices */
@media screen and (max-width: 480px) {
    .post-detail-container {
        margin: 10px; /* Reduce margin for mobile */
        border-radius: 4px; /* Less rounded corners for a cleaner look */
    }

    .back-arrow {
        font-size: 20px; /* Smaller back arrow for mobile */
    }

    .post-username,
    .post-detail-caption {
        font-size: 12px; /* Further reduce font sizes for mobile */
    }
}
